"use client";

import { useState, useEffect, useMemo } from 'react';
import { Download, Mic, Square, Settings, FileText } from 'lucide-react';
import {
  SidebarProvider,
  Sidebar,
  SidebarInset,
  SidebarHeader,
  SidebarContent,
  SidebarTrigger,
  SidebarMenu,
  SidebarMenuItem,
  SidebarMenuButton,
} from '@/components/ui/sidebar';
import { Button } from '@/components/ui/button';
import { Card, CardContent, CardHeader, CardTitle } from '@/components/ui/card';
import { Tabs, TabsContent, TabsList, TabsTrigger } from '@/components/ui/tabs';
import { Skeleton } from '@/components/ui/skeleton';
import { useToast } from '@/hooks/use-toast';
import { useRecorder, type RecordingState } from '@/hooks/use-recorder';
import { exportToSrt } from '@/lib/srt';
import type { TranscriptWord } from '@/lib/types';
import { EditorView } from './editor-view';
import { TranscriptView } from './transcript-view';

const mockTranscript: TranscriptWord[] = [
    { text: "Welcome", start: 0.5, end: 1.2 }, { text: "to", start: 1.2, end: 1.4 }, { text: "VoiceScribe,", start: 1.4, end: 2.5 },
    { text: "your", start: 2.7, end: 3.0 }, { text: "personal", start: 3.0, end: 3.6 }, { text: "transcription", start: 3.6, end: 4.5 }, { text: "assistant.", start: 4.5, end: 5.2 },
    { text: "You", start: 6.0, end: 6.2 }, { text: "can", start: 6.2, end: 6.5 }, { text: "record", start: 6.5, end: 7.0 },
    { text: "your", start: 7.0, end: 7.2 }, { text: "voice,", start: 7.2, end: 7.6 }, { text: "edit", start: 7.8, end: 8.2 },
    { text: "the", start: 8.2, end: 8.4 }, { text: "text,", start: 8.4, end: 8.8 }, { text: "and", start: 9.0, end: 9.2 },
    { text: "export", start: 9.2, end: 9.7 }, { text: "it", start: 9.7, end: 9.9 }, { text: "as", start: 9.9, end: 10.1 },
    { text: "an", start: 10.1, end: 10.3 }, { text: "SRT", start: 10.3, end: 10.7 }, { text: "file.", start: 10.7, end: 11.2 },
];

export function VoiceScribePage() {
  const { recordingState, startRecording, stopRecording, audioBlob, audioUrl, error } = useRecorder();
  const [isTranscribing, setIsTranscribing] = useState(false);
  const [structuredTranscript, setStructuredTranscript] = useState<TranscriptWord[]>([]);
  const [editedTranscript, setEditedTranscript] = useState('');
  const [audioDuration, setAudioDuration] = useState(0);

  const { toast } = useToast();

  useEffect(() => {
    if (error) {
      toast({
        title: 'Recording Error',
        description: error,
        variant: 'destructive',
      });
    }
  }, [error, toast]);

  useEffect(() => {
    if (audioBlob) {
      const audio = new Audio(URL.createObjectURL(audioBlob));
      audio.onloadedmetadata = () => setAudioDuration(audio.duration);

      setIsTranscribing(true);
      // Simulate AI transcription
      setTimeout(() => {
        setStructuredTranscript(mockTranscript);
        setEditedTranscript(mockTranscript.map(w => w.text).join(' '));
        setIsTranscribing(false);
      }, 2000);
    }
  }, [audioBlob]);

  const handleExport = () => {
    if (editedTranscript && audioDuration > 0) {
      exportToSrt(editedTranscript, audioDuration);
    } else {
      toast({
        title: 'Export Failed',
        description: 'There is no transcript to export.',
        variant: 'destructive',
      });
    }
  };

  const recordButtonText = useMemo(() => {
    const stateMap: Record<RecordingState, string> = {
      idle: 'Record',
      permission_pending: 'Waiting...',
      recording: 'Stop',
      stopped: 'Record Again',
      error: 'Record'
    };
    return stateMap[recordingState];
  }, [recordingState]);

  const isReadyForContent = !isTranscribing && structuredTranscript.length > 0;

  return (
    <SidebarProvider>
      <Sidebar>
        <SidebarHeader>
            <div className="flex items-center gap-2 p-2">
                <FileText className="text-primary"/>
                <h2 className="text-lg font-semibold">VoiceScribe</h2>
            </div>
        </SidebarHeader>
        <SidebarContent>
            <SidebarMenu>
                <SidebarMenuItem>
                    <SidebarMenuButton tooltip="Settings" isActive>
                        <Settings />
                        <span>Settings</span>
                    </SidebarMenuButton>
                </SidebarMenuItem>
            </SidebarMenu>
        </SidebarContent>
      </Sidebar>
      <SidebarInset>
        <div className="p-4 md:p-8">
            <header className="flex items-center justify-between mb-8">
                <div>
                    <h1 className="text-3xl font-bold tracking-tight">Transcription Studio</h1>
                    <p className="text-muted-foreground">Record, edit, and export your audio with ease.</p>
                </div>
                <div className="md:hidden">
                    <SidebarTrigger />
                </div>
            </header>
          <Card className="min-h-[70vh]">
            <CardHeader>
              <Tabs defaultValue="edit" className="w-full">
                <TabsList className="grid w-full grid-cols-2">
                  <TabsTrigger value="edit">Editor</TabsTrigger>
                  <TabsTrigger value="sync">Sync Preview</TabsTrigger>
                </TabsList>
                <CardContent className="pt-6">
                  {isTranscribing ? (
                     <div className="space-y-4 pt-4">
                        <Skeleton className="h-8 w-1/4" />
                        <Skeleton className="h-48 w-full" />
                        <Skeleton className="h-12 w-full" />
                     </div>
                  ) : (
                    <>
                      {structuredTranscript.length === 0 ? (
                        <div className="flex flex-col items-center justify-center text-center h-[50vh]">
                            <Mic className="w-16 h-16 text-muted-foreground/50 mb-4"/>
                            <h3 className="text-xl font-semibold">Ready to Transcribe</h3>
                            <p className="text-muted-foreground">Click the record button in the right panel to start.</p>
                        </div>
                      ) : (
                        <>
                         <TabsContent value="edit" className="m-0">
                            <EditorView
                                transcript={editedTranscript}
                                onTranscriptChange={setEditedTranscript}
                                disabled={!isReadyForContent}
                            />
                        </TabsContent>
                        <TabsContent value="sync" className="m-0">
                            <TranscriptView
                                structuredTranscript={structuredTranscript}
                                audioUrl={audioUrl}
                            />
                        </TabsContent>
                        </>
                      )}
                    </>
                  )}
                </CardContent>
              </Tabs>
            </CardHeader>
          </Card>
        </div>
      </SidebarInset>
      <Sidebar side="right">
        <SidebarHeader>
            <h3 className="text-lg font-semibold text-center">Actions</h3>
        </SidebarHeader>
        <SidebarContent className="p-4 flex flex-col gap-4">
            <Button 
                onClick={recordingState === 'recording' ? stopRecording : startRecording} 
                disabled={recordingState === 'permission_pending' || isTranscribing}
                className="w-full"
            >
                {recordingState === 'recording' ? <Square className="mr-2 h-4 w-4" /> : <Mic className="mr-2 h-4 w-4" />}
                {recordButtonText}
            </Button>
            <Button 
                onClick={handleExport}
                variant="outline"
                disabled={!isReadyForContent}
                className="w-full"
            >
                <Download className="mr-2 h-4 w-4" />
                Export to SRT
            </Button>
        </SidebarContent>
      </Sidebar>
    </SidebarProvider>
  );
}
