"use client";

import { useState, useRef, useEffect } from 'react';
import type { TranscriptWord } from '@/lib/types';
import { Play, Pause } from 'lucide-react';
import { Button } from '@/components/ui/button';
import { ScrollArea } from '@/components/ui/scroll-area';
import { Slider } from '@/components/ui/slider';

interface TranscriptViewProps {
  structuredTranscript: TranscriptWord[];
  audioUrl: string | null;
}

export function TranscriptView({ structuredTranscript, audioUrl }: TranscriptViewProps) {
  const [isPlaying, setIsPlaying] = useState(false);
  const [currentTime, setCurrentTime] = useState(0);
  const [duration, setDuration] = useState(0);
  const audioRef = useRef<HTMLAudioElement | null>(null);
  const activeWordRef = useRef<HTMLSpanElement | null>(null);

  useEffect(() => {
    if (audioRef.current && audioUrl) {
      audioRef.current.src = audioUrl;
    }
  }, [audioUrl]);

  useEffect(() => {
    if (activeWordRef.current) {
      activeWordRef.current.scrollIntoView({
        behavior: 'smooth',
        block: 'center',
      });
    }
  }, [currentTime]);

  const handlePlayPause = () => {
    if (audioRef.current) {
      if (isPlaying) {
        audioRef.current.pause();
      } else {
        audioRef.current.play();
      }
      setIsPlaying(!isPlaying);
    }
  };

  const handleTimeUpdate = () => {
    if (audioRef.current) {
      setCurrentTime(audioRef.current.currentTime);
    }
  };

  const handleLoadedMetadata = () => {
    if (audioRef.current) {
      setDuration(audioRef.current.duration);
    }
  };
  
  const handleSeek = (value: number[]) => {
    if(audioRef.current) {
      audioRef.current.currentTime = value[0];
      setCurrentTime(value[0]);
    }
  }

  return (
    <div className="flex h-full flex-col gap-4">
      <ScrollArea className="h-[calc(50vh-80px)] min-h-[220px] w-full rounded-md border p-4">
        <p className="text-base leading-relaxed">
          {structuredTranscript.map((word, index) => {
            const isActive = currentTime >= word.start && currentTime < word.end;
            return (
              <span
                key={index}
                ref={isActive ? activeWordRef : null}
                className={`transition-colors duration-150 ${
                  isActive ? 'bg-accent/50 rounded-md p-1' : ''
                }`}
              >
                {word.text}{' '}
              </span>
            );
          })}
        </p>
      </ScrollArea>

      <div className="flex items-center gap-4 rounded-md border p-4">
        <Button onClick={handlePlayPause} size="icon" variant="ghost" disabled={!audioUrl}>
          {isPlaying ? <Pause className="h-6 w-6" /> : <Play className="h-6 w-6" />}
        </Button>
        <Slider
          value={[currentTime]}
          max={duration}
          step={0.1}
          onValueChange={handleSeek}
          disabled={!audioUrl}
        />
        <audio
          ref={audioRef}
          onTimeUpdate={handleTimeUpdate}
          onLoadedMetadata={handleLoadedMetadata}
          onPlay={() => setIsPlaying(true)}
          onPause={() => setIsPlaying(false)}
          onEnded={() => setIsPlaying(false)}
          className="hidden"
        />
      </div>
    </div>
  );
}
